.b-main {
  margin-top: 110px;
  &__container {
    max-width: 875px;
    @include mx--auto;
    color: $light;
    @include media(lg) {
      max-width: 905px;
      @include mx--auto;
    }
    @include media(md) {
      max-width: none;
      padding: 0 9%;
    }
    @include media(sm) {
      padding: 0 8%;
    }
  }
  &__title {
    @include font(82, bold, true);
    margin-bottom: 20px;
    font-family: $Bebas;
    max-width: 65%;
    @include media(lg) {
      @include font(62);
      line-height: rem(62);
    }
    @include media(sm) {
      @include font(42);
      line-height: rem(42);
      margin-bottom: 30px;
      max-width: 65%;
    }
  }
  &__text {
    @include font(18);
    line-height: rem(21);
    display: block;
    margin-bottom: 110px;
    @include media(lg) {
      margin-bottom: 60px;
    }
    @include media(sm) {
      margin-bottom: 50px;
    }
  }
  &__card {
    @include size(427px, 241px);
    overflow-x: hidden;
    &:hover {
      background: #131313;
      .b-cards__img {
        svg path{
          stroke: $light;
        }
      }
      .b-button {
        background: #0037A0;
      }
    }
    @include media(md) {
      @include size(304px, 241px);
    }
    @include media(sm) {
      width: 100%;
    }
  }
  &__button {
    margin-top: 40px;
    @include media(md) {
      margin-top: 60px;
    }
    @include media(xs) {
      margin-top: 50px;
    }
  }
  @include media(sm) {
    margin-top: 66px;
  }
}
