.b-popup {
  opacity: 0;
  visibility: hidden;

  &--active {
    visibility: visible;
    opacity: 1;
  }
  &__overlay {
    position: absolute;
    top: 0;
    left: 0;
    background: $black;
    width: 100%;
    height: 100%;
    display: block;
  }

  &__content {
    color: $light;
    width: 100%;
    height: auto;
    &--thanks {
      @include align(absolute, false, true);
      top: 140px;

    }
    &--dark {
      @include mx--auto;
      margin-top: 100px;
      background: $dark-grey;
      border: 1px solid rgba($light, 0.1);
      padding: 46px 70px 60px;
      @extend %shadow;
      width: 872px;
      border-radius: 4px;
      @include media(xl) {
        width: 100%;
        max-width: 905px;
      }
      @include media(md) {
        padding: 46px 40px 60px;
        min-height: auto;
        margin-bottom: 70px;
        margin-top: 60px;
      }
      @include media(sm) {
        padding: 46px 20px 60px;
      }
    }
  }

  &__row {
    margin-bottom: 22px;
    &--active {
      .b-popup__name {
        color: $light;
      }
      .b-popup__header {
        &::after {
          background: $main;
        }
      }
      .b-popup__form {
        display: block;
      }
      .b-popup__sum {
        display: block;
      }
      .b-popup__pay {
        @include flex(center, center);
      }
    }
  }

  &__header {
    @include flex(center, space-between);
    padding-bottom: 10px;
    position: relative;
    color: #494949;

    &::after {
      @include pseudo;
      bottom: -10px;
      @include size(100%, 4px);
      border-radius: 30px;
      background: #494949;
      color: #494949;
      @include trs;
    }

  }
  &__step {
    @include font(16, 300);
    font-family: $RobotoLight;
    line-height: rem(21);
    display: block;
    margin-bottom: 5px;
    color: #494949;
  }

  &__name {
    @include font(24, bold, true);
    font-family: $Bebas;
    line-height: rem(24);
    @include trs;
  }

  &__form {
    margin-top: 60px;
    max-width: 426px;
    @include mx--auto;
    display: none;
  }

  &__label {
    width: 100%;
    height: 40px;
    display: block;
    margin-bottom: 40px;
    position: relative;
    &:last-child {
      margin-bottom: 0;
    }
    &::before {
      @include triangle($light, down, 5px);
      right: 11px;
      top: 50%;
      transform: translateY(-50%);
    }

    &--active {
      .b-popup__input {
        border-bottom-color: $main;
        color: $light;
      }
      .b-popup__hidden {
        opacity: 1;
        visibility: visible;
      }
      &::before {
        transform: rotate(180deg) translateY(-50%);
      }
    }
  }

  &__input {
    display: block;
    width: 100%;
    height: 40px;
    padding-left: 20px;
    background: none;
    border: none;
    border-radius: 0;
    -webkit-appearance: none;
    border-bottom: 2px solid #C4C4C4;
    color: #818181;
    @include font(16, 300);
    font-family: $RobotoLight;
    padding-bottom: 6px;
    cursor: pointer;
    @include trs;
    position: relative;
  }

  &__hidden,&__error {
    position: absolute;
    left: 20px;
    @include font(12, 300);
    font-family: $RobotoLight;
    line-height: 16px;
    @include trs;
    visibility: hidden;
    opacity: 0;
  }
  &__hidden {
    top: -15px;
  }
  &__error {
    bottom: -20px;
    color: #868686;
    &--active {
      visibility: visible;
      opacity: 1;
    }
  }
  &__change {
    display: none;
  }
  &__button {
    @include size(100%, 44px);
    background: $main;
    @include trs;
    margin-top: 40px;
    &:hover {
      background: $hover;
    }
  }
  &__prev {
    margin-top: 25px;
  }
  &__text {
    @include font(14,300);
    line-height: rem(18);
    color: #818181;
    font-family: $RobotoLight;
    margin-bottom: 10px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__sum {
    display: none;
    text-align: right;
    @include media(sm) {
      position: absolute;
      bottom: -70px;
      text-align: left;
    }
  }
  &__pay {
    margin-top: 40px;
    display: none;
    @include media(sm) {
      margin-top: 100px;
    }
  }
  &__card {
    @include size(160px, 98px);
    border: 1px solid $light;
    background: $grey--second;
    border-radius: 4px;
    cursor: pointer;
    margin-right: 20px;
    @include flex(center, center);
    @include trs;
    &:hover {
      background: #0D0D0D;
      border-color: #494949;
    }
    @include media(sm) {
      @include size(133px, 98px);
      margin-right: 9px;
      &:last-child {
        margin-right: 0;
      }
    }
  }
  &__cardName {
    @include font(14, bold, true);
    font-family: $Bebas;
  }
  &__icon {
    @include size(78px);
    @extend %circle;
    background: $grey--second;
    @include flex(center, center);
    margin-bottom: 30px;
    @include mx--auto;
  }
  &__title {
    @include font(62, bold, true);
    font-family: $Bebas;
    margin-bottom: 60px;
    @include mx--auto;
    text-align: center;
    @include media(sm) {
      @include font(42);
    }
  }
  &__link {
    @include size(120px, 44px);
    background: $main;
    @include flex(center, center);
    border: 1px solid rgba($light, 0.1);
    @include trs;
    @include mx--auto;
    &:hover {
      background: $hover;
    }
  }
  @include media(lg) {
    padding: 0 8%;
  }
}
