.b-nav {
  @include flex(center);
  color: $light;
  font-family: $Bebas;
  z-index: 3;
  &__link {
    @include font(14, bold, true);
    color: $grey--disabled;
    margin-right: 20px;
    position: relative;
    &:last-child {
      margin-right: 40px;
    }
    &::after {
      @include pseudo;
      @include align(absolute, false, true);
      height: 3px;
      width: 0;
      bottom: -10px;
      position: absolute;
      background: $main;
      transition: width .3s ease-in-out;
    }
    &::before {
      @include pseudo;
      @include size(15px, 0);
      @include align(absolute, false, true);
      top: -25px;
      background: url("../img/logo-mini.svg");
      transition: height .3s ease-in-out;
    }
    &:hover, &--active {
      color: $light;
      &::after {
        width: 100%;
      }
      &::before {
        height: 18px;
      }
    }
    @include media(lg) {
      @include font(51);
      line-height: rem(51);
      display: table;
      margin-bottom: 25px;
      margin-right: 0;
      &:last-child {
        margin-bottom: 0;
      }
      &::after {
        transition: none;
      }
      &::before {
        display: none;
      }
      &:hover{
        &::after {
          display: none;
        }
      }
      &--active {
        &::after {
          width: 100%;
          height: 8px;
          bottom: -4px;
        }
      }
    }
    @include media(xsm) {
      @include font(40);
      line-height: rem(40);
      margin-bottom: 15px;
    }
  }
  &__button {
    @include media(lg) {
      @include size(318px, 52px);
      margin-top: 140px;
      max-width: 100%;
      span {
        @include font(16)
      }
    }
    @include media(sm) {
      width: 100%;
      @include mx--auto;
    }
    @include media(xsm) {
      margin-top: 100px;
    }
  }
  @include media(lg) {
    flex-direction: column;
    align-items: flex-start;
    background: #141414;
    width: 100%;
    @include align(fixed);
    left: -1000%;
    overflow-x: hidden;
    z-index: 2;
    top: 0;
    padding-top: 236px;
    padding-left: 6%;
    height: 100%;
    @include trs;
    &--active {
      left: 0;
    }
  }
  @include media(sm){
    padding-left: 30px;
    padding-right: 30px;
  }
  @include media(xsm) {
    position: absolute;
    overflow-y: auto;
  }
}
