.b-callback {
  top: 160px;
  width: 872px;
  @include align(absolute, false, true);
  &__container {
    background: #1C1C1C;
    border: 1px solid rgba($black, .1);
    @extend %shadow;
    border-radius: 4px;
    padding: 120px 183px 133px;
    text-align: center;
    @include media(md) {
      padding: 120px 62px 133px;
    }
    @include media(sm) {
      text-align: left;
      padding: 70px 15px 138px;
    }
  }
  &__title {
    @include font(42,bold,true);
    font-family: $Bebas;
    line-height: rem(42);
    margin-bottom: 10px;
  }
  &__text {
    @include font(18);
  }
  &__form {
    margin-top: 40px;
    max-width: 426px;
    @include mx--auto;
  }
  &__input {
    border: none;
    border-bottom: 2px solid #c4c4c4;
    width: 100%;
    background: none;
    box-shadow: none;
    border-radius: 0;
    color: $light;
    @include font(16, 300);
    font-family: $RobotoLight;
    text-align: center;
    &::placeholder {
      color: #818181;
    }
    @include media(sm) {
      text-align: left;
      padding-left: 10px;
    }
  }
  &__button {
    margin-top: 40px;
    @include size(100%, 44px);
    background: $main;
    @include trs;
    &:hover {
      background: $hover;
    }
  }
  @include media(xl) {
    width: 100%;
    padding: 0 60px;
  }
  @include media(md) {
    padding: 0 70px;
  }
  @include media(sm) {
    padding: 0 30px;
  }
}
