.b-radio {
  @include flex(center);
  display: inline-flex;
  position: relative;
  margin-bottom: 10px;

  &__input {
    position: absolute;
    z-index: -1;
    opacity: 0;

    &:checked + .b-radio__text::after {
      opacity: 1;
    }
    &:checked + .b-radio__text::before {
      border-color: $main;
    }
  }

  &__text {
    @include font(14);
    position: relative;
    cursor: pointer;
    @include trs;
    padding-left: 30px;
    text-align: left;
    color: rgba($light, .4);
    &.b-error {
      &::before {
        border-color: $error;
      }
    }
    &::before, &::after {
      @include pseudo;
      @include trs;
      @extend %circle;
    }
    &::before {
      @include size(16px);
      @include border(1, $light);
      background: transparent;
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
    }

    &::after {
      position: absolute;
      @include size(8px);
      background: $light;
      @include border(1, $light);
      top: 50%;
      transform: translateY(-50%);
      opacity: 0;
      left: 4px;
      @include trs;
    }

  }
}
