.page {
  min-height: 100vh;
  height: 100%;
  max-width: 100vw;
  position: relative;
  font-family: $Roboto;
  color: $light;
  &--main {
    background: url("../img/bg/main-bg.jpg") no-repeat;
    background-size: cover;
    @include media(sm) {
      background: url("../img/bg/main-bg.jpg") no-repeat 65% top;
      background-size: 178%;
    }
  }
  &--private {
    background: url("../img/bg/private-bg.jpg") no-repeat;
    background-size: cover;
  }
  &--company {
    background: url("../img/bg/company-bg.jpg") no-repeat;
    background-size: cover;
  }
  &--callback {
    background: url("../img/bg/callback-bg.jpg") no-repeat center center;
    background-size: cover;
  }
}
