.b-footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  color: $grey--stroke;
  &__content {
    width: 1320px;
    height: 70px;
    @include mx--auto;
    @include flex(center);
    @include media(xl) {
      width: 100%;
      padding: 0 60px;
    }
    @include media(md) {
      padding: 0 8%;
    }
    @include media(sm) {
      padding: 0 30px;
    }
  }
  &__text {
    @include font(14);
    font-family: $Helvetica;
    line-height: 14px;
    margin-left: 14px;
  }
  @include media(md) {
    position: relative;
  }
}
