.b-button {
  @include flex(center, center);
  @include trs;
  border: none;
  box-shadow: none;
  border-radius: 4px;
  cursor: pointer;
  text-align: center;
  &__content {
    font-family: $Bebas;
    color: $light;
    @include font(14, bold, true);
    line-height: rem(14);
    display: block;
    @include mx--auto;
    @include trs;
    text-align: center;
  }

  &--outline {
    background-color: transparent;
    @include border(1, $light);
    @include size(180px, 44px);
    @include trs;
    &:hover {
      background-color: $light;
      span {
        color: $dark;
      }
    }
  }

  &--main {
    @include size(120px, 40px);
    background: $main;
    &:hover {
      background: $hover;
    }
    span {
      margin-left: auto;
    }
    svg {
      margin-left: auto;
      margin-right: 15px;
      path{
        stroke: $light;
      }

    }
  }

  &--transparent {
    justify-content: flex-start;
    @include trs;
    svg {
      margin-left: 10px;
      path{
        stroke: $main;
        @include trs;
      }

    }
    &:hover {
      span {
        color: #616161;
      }
      svg path {
        stroke: #0C3CE8;
      }
    }
  }
  &--change {
    @include size(120px, 34px);
    border: 1px solid rgba($light, .1);
    @include trs;
    &:hover {
      background: $main;
    }
  }
}
