.b-prices {
  &__container {
    max-width: 982px;
    @include mx--auto;
    margin-top: 120px;
    @include media(lg) {
      max-width: none;
      margin-top: 60px;
      padding: 0 60px;
    }
    @include media(md) {
      width: 100%;
      padding: 0 8%;
      margin-top: 90px;
    }
    @include media(sm) {
      padding: 0 30px;
    }
  }
  &__title {
    @include title--underline('');
  }
  &__cards {
    flex-wrap: wrap;
    @include media(lg) {
      margin-bottom: 30px;
    }
  }
  &__card {
    @include size(314px, 270px);
    margin-right: 20px;
    margin-bottom: 20px;
    cursor: pointer;
    &:nth-child(3n) {
      margin-right: 0;
    }
    &:hover {
      background: #131313;
      .b-cards__icon {
        svg path{
          stroke: $light;
        }
      }
      .b-button__content {
        color: #616161;
      }
    }
    @include media(lg) {
      @include size(32%, 270px);
      margin-right: 1%;
    }
    @include media(md) {
      @include size(49%, 250px);
      &:nth-child(3n) {
        margin-right: 2%;
      }
      &:nth-child(2n) {
        margin-right: 0;
      }
    }
    @include media(sm) {
      width: 100%;
    }
  }
  &__price {
    color: #868686;
  }
  &__button {
    margin-top: 30px;
  }
}
