.b-header {
  max-height: 48px;
  padding-top: 26px;
  &__container {
    @include flex(center, space-between);
    width: 100%;
    position: relative;
    &::after {
      @include pseudo;
      position: absolute;
      width: 100%;
      height: 1px;
      background: rgba($light, .1);
      bottom: -26px;
      z-index: 3;
    }
    @include media(xsm) {
      position: static;
      &::before {
        display: none;
      }
    }
  }


  @include media(sm) {
    padding-top: 20px;
  }
}
