$fontPath: '../fonts/';
@font-face {
  font-family: 'Roboto';
  src: url('#{$fontPath}RobotoRegular/RobotoRegular.eot');
  src: url('#{$fontPath}RobotoRegular/RobotoRegular.eot?#iefix') format('embedded-opentype'),
  url('#{$fontPath}RobotoRegular/RobotoRegular.woff') format('woff'),
  url('#{$fontPath}RobotoRegular/RobotoRegular.ttf') format('truetype'),
  url('#{$fontPath}RobotoRegular/RobotoRegular.svg') format('svg');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Roboto Light';
  src: url('#{$fontPath}RobotoLight/roboto_light.eot');
  src: url('#{$fontPath}RobotoLight/roboto_light.eot?#iefix') format('embedded-opentype'),
  url('#{$fontPath}RobotoLight/roboto_light.woff') format('woff'),
  url('#{$fontPath}RobotoLight/roboto_light.ttf') format('truetype'),
  url('#{$fontPath}RobotoLight/roboto_light.svg') format('svg');

}
@font-face {
  font-family: 'Bebas Neue Bold';
  src: url('#{$fontPath}BebasNeue-Bold/bebas_neue_bold.eot');
  src: url('#{$fontPath}BebasNeue-Bold/bebas_neue_bold.eot?#iefix') format('embedded-opentype'),
  url('#{$fontPath}BebasNeue-Bold/bebas_neue_bold.woff') format('woff'),
  url('#{$fontPath}BebasNeue-Bold/bebas_neue_bold.ttf') format('truetype'),
  url('#{$fontPath}BebasNeue-Bold/bebas_neue_bold.svg') format('svg');
  font-weight: bold;
}
@font-face {
  font-family: 'Helvetica Neue';
  src: url('#{$fontPath}HelveticaNeue/HelveticaNeue.eot');
  src: url('#{$fontPath}HelveticaNeue/HelveticaNeue.eot?#iefix') format('embedded-opentype'),
  url('#{$fontPath}HelveticaNeue/HelveticaNeue.woff') format('woff'),
  url('#{$fontPath}HelveticaNeue/HelveticaNeue.ttf') format('truetype'),
  url('#{$fontPath}HelveticaNeue/HelveticaNeue.svg') format('svg');
  font-weight: normal;
  font-style: normal;
}
$Bebas: 'Bebas Neue Bold', sans-serif;
$Roboto: 'Roboto', sans-serif;
$RobotoLight: 'Roboto Light', sans-serif;
$Helvetica: 'Helvetica Neue', sans-serif;
