.b-about {
  overflow-x: hidden;
  margin-top: 85px;
  &__container {
    max-width: 1095px;
    @include mx--auto;
    @include flex(center, space-between);
    @include media(xl) {
      max-width: none;
      padding: 0 60px;
    }
    @media (max-width: 970px) {
      flex-wrap: wrap;
    }
    @include media(md) {
      max-width: none;
      display: block;
      width: 100%;
      padding: 0 70px;
    }
    @include media(sm) {
      padding: 0 30px;
    }
  }
  &__title {
    @include title--underline(' ');
    @include media(lg) {
      margin-top: 60px;
    }
  }
  &__info {
    width: 456px;
    margin-right: 109px;
    @include media(lg) {
      margin-right: 58px;
      width: 455px;
    }
    @include media(md) {
      width: 100%;
    }
  }
  &__content {
    font-family: $Helvetica;
    @include font(16);
    line-height: rem(24);
    @include media(md) {
      @include flex;
      margin-bottom: 70px;
    }
    @include media(sm) {
      display: block;
    }
  }
  &__text {
    margin-bottom: 30px;
    &--lighten {
      color: #454545;
      @include media(lg) {
        width: 444px;
      }
      @include media(md) {
        margin-left: 35px;
        br {
          display: block !important;
        }
      }
      @include media(sm) {
        margin-left: 0;
      }
    }
    @include media(md) {
      width: 293px;
      br:first-child {
        display: none;
      }
    }
  }
  &__img {
    @include size(538px, 540px);
    margin-top: 105px;
    img  {
      max-width: none;
    }
    @include media(lg) {
      @include size(43%, 598px);
    }
    @include media(md) {
      position: relative;
      top: 0;
      right: 0;
      @include size(628px, 368px);
    }
    @include media(sm) {
      @include size(100%, 360px);
      img {
        @include size(100%, 360px);

      }
    }
  }
  @include media(lg) {
    position: relative;
    margin-top: 60px;
  }
}
