.b-burger {
  display: none;
  z-index: 3;
  border: none;
  background: transparent;
  box-shadow: none;
  width: 26px;
  height: 12.5px;
  position: relative;
  transform: rotate(0);
  transition: .5s ease-in-out;
  cursor: pointer;
}

.b-burger span {
  display: block;
  position: absolute;
  height: 3.25px;
  width: 100%;
  transform: rotate(0);
  transition: .25s ease-in-out;
  opacity: 1;
  left: 0;
  background: #fff;
}

.b-burger span:nth-child(1) {
  top: 0;
  transform-origin: center center;
}

.b-burger span:nth-child(2) {
  top: 100%;
  transform-origin: center center;
}

.b-burger--active span:nth-child(1) {
  transform: translate(0, -50%) rotate(45deg);
  top: 50%;
}

.b-burger--active span:nth-child(2) {
  transform: translate(0, -50%) rotate(-45deg);
  top: 50%;
}

