.b-cards {
  @include flex;
  justify-content: space-between;
  @include media(sm) {
    display: block;
  }
  &__item {
    padding: 40px 0 34px 30px;
    background: $dark-grey;
    @include trs;
    color: $light;
    position: relative;
    margin-right: 50px;
    border-radius: 4px;
    @include border(1, rgba($black, .1));
    @extend %shadow;

    &:last-child {
      margin-right: 0;
    }

    @include media(md) {
      margin-right: 20px;
      &:last-child {
        margin-right: 0;
      }
    }
    @include media(sm) {
      width: 100%;
      display: block;
      margin-right: 0;
      margin-bottom: 20px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__title {
    @include font(24, false, true);
    line-height: rem(24);
    font-family: $Bebas;
  }

  &__text {
    margin: 10px 0 19px;
    @include font(16);
    line-height: rem(21);
    font-family: $RobotoLight;
    height: 60px;
    color: #D6D6D6;
    @include media(md) {
      margin: 10px 0 23px;
    }
  }

  &__img {
    @include size(216px);
    @include align(absolute, true);
    right: -51px;
    @extend %circle;
    @include flex(center, center);
    background: $grey--second;

    svg {
      path {
        stroke: $grey--stroke;
      }
    }

    @include media(md) {
      display: none;
    }
  }

  &__icon {
    @include size(60px);
    background: $grey--second;
    @include flex(center, center);
    @extend %circle;
    margin-bottom: 15px;

    &--large {
      @include size(78px);
      @include mx--auto;
      margin-bottom: 20px;
    }
  }

  &__subtitle {
    @include font(18);
    line-height: rem(21);
  }

  &__svg {
    &--large {
      margin-right: 22px;
    }
  }
}
