.b-burger {
  display: none;
  z-index: 3;
  border: none;
  background: transparent;
  box-shadow: none;
  @include size(26px,12.5px);
  position: relative;
  transform: rotate(0);
  transition: .5s ease-in-out;
  cursor: pointer;
  span {
    display: block;
    position: absolute;
    height: 3.25px;
    width: 100%;
    transform: rotate(0);
    transition: .25s ease-in-out;
    opacity: 1;
    left: 0;
    background: $light;
    &:nth-child(1) {
      top: 0;
      transform-origin: center center;
    }
    &:nth-child(2) {
      top: 100%;
      transform-origin: center center;
    }
  }

  &--active {
    span {
      &:nth-child(1) {
        transform: translate(0, -50%) rotate(45deg);
        top: 50%;
      }

      &:nth-child(2) {
        transform: translate(0, -50%) rotate(-45deg);
        top: 50%;
      }
    }
  }
  @include media(lg) {
    display: block;
    margin-right: 32px;
  }
  @include media(sm) {
    margin-right: 0;
  }
}
