.b-info {
  .container {
    margin-top: 120px;
    text-align: center;
    @include media(md) {
      margin-top: 92px;
    }
  }
  &__title {
    font-family: $Bebas;
    @include font(62, bold, true);
    line-height: rem(62);
    margin-bottom: 20px;
    @include media(sm) {
      @include font(42);
      line-height: rem(42);
      text-align: left;
    }
  }
  &__description {
    @include font(18);
    line-height: rem(21);
    max-width: 50%;
    @include mx--auto;
    margin-bottom: 20px;
    @include media(md) {
      max-width: 90%;
      margin-bottom: 25px;
    }
    @include media(sm) {
      text-align: left;
      max-width: none;
    }
  }
  &__buttons {
    width: 100%;
    @include flex(center, center);
    margin-bottom: 60px;
    @include media(md) {
      margin-bottom: 40px;
    }
  }
  .b-button--main {
    border: 1px solid rgba(255, 255, 255, 0.1);
    height: 44px;
    margin-right: 20px;
  }
  &__cards {
    flex-wrap: wrap;
    @include media(sm) {
      align-items: center;
    }
  }
  &__card {
    @include size(315px, 241px);
    padding-left: 30px;
    padding-right: 30px;
    margin-right: 20px;
    background: $dark-grey;
    cursor: pointer;
    @include trs;
    &:nth-child(4n) {
      margin-right: 0;
    }
    &:hover {
      background: #0D0D0D;
      border-color: #494949;
    }
    @include media(lg) {
      @include size(32%, 241px);
      &:nth-child(3n) {
        margin-right: 0;
      }
      &:nth-child(4n) {
        margin-right: 2%;
      }
      margin-bottom: 2%;
      margin-right: 2%;
      padding: 30px 15px;
    }
    @include media(md) {
      @include size(49%, 241px);
      &:nth-child(3n) {
        margin-right: 2%;
      }
      &:nth-child(2n) {
        margin-right: 0;
      }
    }
    @include media(sm) {
      width: 100%;
    }
  }
}
