.nice-select {
  &::after {
    display: none;
  }
  & .list {
    left: calc(100% - 225px);
    width: 225px;
    height: 150px;
    border-radius: 4px;
    padding: 25px;
    li:first-child {
      display: none;
    }
    @include media(sm) {
      width: 100%;
      left: 0;
    }
  }
  & .option {
    width: 100%;
    padding: 0;
    @include font(16);
    line-height: 20px;
    color: #494949;
    height: auto;
    min-height: auto;
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }
    &:hover {
      color: $main;
    }
  }
}
